* {
  box-sizing: border-box;
}


body {
  margin: 0;
  padding: 0;
  font-family: 'Avenir Next', sans-serif;
}


h1,
h2,
h3,
h4,
p {
  margin: 0;
}


/* We use display flex to separate the 2 views we are going to be building
We fixed our width and height for our desktop display
We apply some top and bottom margin to separate the view from the corners and auto left and right to place it in the middle
Finally, we applied a red color to the background */

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 650px;
  height: 400px;
  padding-left: 10px;
  margin: 50px auto;
  background-color: #FE0065;
  border-radius: 10px;
  box-shadow: 0 14px 45px rgba(0, 0, 0, 0.22), 0 18px 45px rgba(0, 0, 0, 0.22);
}

.poke-cell {
    width: 120px;
    height: 120px;
    margin: 10px;
    background-color: #FFF;
    background-repeat: no-repeat;
    border: none;
    border-radius: 5px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16);
  }


/* We are using display flex to take advantage of the wrapping functionality since we are going to wrap all the PokeCell buttons inside.
We set the width to 50% to fill the left half of the app component
We also included overflow scroll because not all of the PokeCells are going to be displayed at once.
 */

.poke-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    width: 50%;
    height: 80%;
    padding: 10px;
    margin-right: 10px;
    background-color: #BFF9FF;
    box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.5);
    overflow: scroll;
  }
.detail-view {
    width: 50%;
    height: 100%;
    padding: 10px;
    background-color: #df095e;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .sprite-image {
    width: 80%;
    height: 150px;
    margin: 10px auto;
    background-color: #585858;
    border: 15px solid #DEDEDE;
    border-radius: 5px;
    color: #585858;
  }
  
  .data-wrapper {
    width: 80%;
    height: 110px;
    padding: 10px;
    margin: 0 auto;
    background-color: #585858;
    border-radius: 5px;
    box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.22);
    color: #FFF;
  }
  
  .data-name {
    margin: 0 0 5px;
  }
  
  .data-char {
    font-size: 10px;
  }
  
  .data-chars {
      font-size: 10px;
  }
